<template>
    <main>
        <loading v-if="loading"></loading>
        <div class="container-fluid mt-4" v-if="fulldata.album_tracks && fulldata.album_tracks.length > 0">
            <div class="row mb-4">
                <div class="col-xl-1 col-lg-2 col-md-3 col-sm-4 col-6">
                    <img v-lazy="fulldata.thumbnail" :alt="fulldata.album" class="img-fluid">
                </div>
                <div class="col">
                    <h3 class="pt-0 pt-md-3" v-text="fulldata.album"></h3>
                    <h6 v-text="fulldata.album_tracks[0].album.artist"></h6>
                </div>
            </div>
            <div class="row">
                <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in fulldata.album_tracks" :key="'Album'+index">
                    <RJDBox :song="data.song" :artist="data.artist" :img="data.photo" :tag="data.type" :data="data"></RJDBox>
                </div>
            </div>
        </div>
    </main>
</template>

<script>
    import RJDBox from './RJDBox.vue';
    import Loading from './Loading';
    import axios from 'axios';
    export default {
        name: "Album",
        components: {
            RJDBox,Loading
        },
        data () {
            return {
                loading : false,
                fulldata : {}
            }
        },
        watch: {
            '$route': 'getData'
        },
        methods: {
            getData () {
                let self = this;
                self.loading = true;
                axios.get(window.API_URL+'/mp3?id='+this.$route.params.id)
                    .then(function (response) {
                        // handle success
                        self.fulldata = response.data;
                        self.loading = false;
                    })
                    .catch(function (error) {
                        // eslint-disable-next-line
                        console.log(error);
                        self.loading = false;
                    });
            }
        },
        created () {
            this.getData();
        }
    }
</script>