<template>
    <main>
        <loading v-if="loading"></loading>
        <div class="container-fluid mt-4">
            <div class="row">
                <div class="col-md-6 col-12 mb-4 slideshow" v-for="(data,index) in slider1" :key="'slideA'+index">
                    <RJDBox :song="data.song" :artist="data.artist" :img="data.photo" :tag="data.type" :data="data.item"></RJDBox>
                </div>
                <div class="col-md-6 col-12 mb-4 slideshow" v-for="(data,index) in slider2" :key="'slideB'+index">
                    <RJDBox :song="data.song" :artist="data.artist" :img="data.photo" :tag="data.type" :data="data.item"></RJDBox>
                </div>
                <div class="col-md-6 col-12 mb-4 slideshow" v-for="(data,index) in slider3" :key="'slideC'+index">
                    <RJDBox :song="data.song" :artist="data.artist" :img="data.photo" :tag="data.type" :data="data.item"></RJDBox>
                </div>
                <div class="col-md-6 col-12 mb-4 slideshow" v-for="(data,index) in slider4" :key="'slideD'+index">
                    <RJDBox :song="data.song" :artist="data.artist" :img="data.photo" :tag="data.type" :data="data.item"></RJDBox>
                </div>
            </div>
        </div>
        <div class="container-fluid" v-if="playlist && playlist.length > 0">
            <div class="row">
                <div class="col">
                    <h3 class="mb-3">Playlist</h3>
                </div>
            </div>
            <div class="row">
                <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in playlist" :key="'Playlist'+index">
                    <RJDBox :song="null" :artist="data.title" :img="data.photo" :tag="'playlist'" :data="data.id"></RJDBox>
                </div>
            </div>
        </div>
        <div class="container-fluid" v-if="newMusic && newMusic.length > 0">
            <div class="row">
                <div class="col">
                    <h3 class="mb-3">New Music</h3>
                </div>
            </div>
            <div class="row">
                <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in newMusic" :key="'New'+index">
                    <RJDBox :song="data.song" :artist="data.artist" :img="data.photo" :tag="data.type" :data="data.item"></RJDBox>
                </div>
            </div>
        </div>
        <div class="container-fluid mt-4" v-if="hotTracks && hotTracks.length > 0">
            <div class="row">
                <div class="col">
                    <h3 class="mb-3">Hot Tracks</h3>
                </div>
            </div>
            <div class="row">
                <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in hotTracks" :key="'Hot'+index">
                    <RJDBox :song="data.song" :artist="data.artist" :img="data.photo" :tag="data.type" :data="data.item"></RJDBox>
                </div>
            </div>
        </div>
        <div class="container-fluid mt-4" v-if="moreMusic && moreMusic.length > 0">
            <div class="row">
                <div class="col">
                    <h3 class="mb-3">More Music</h3>
                </div>
            </div>
            <div class="row">
                <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in moreMusic" :key="'More'+index">
                    <RJDBox :song="data.song" :artist="data.artist" :img="data.photo" :tag="data.type" :data="data.item"></RJDBox>
                </div>
            </div>
        </div>
    </main>
</template>

<script>
import RJDBox from './RJDBox.vue';
import Loading from './Loading';
import axios from 'axios';

export default {
    name: 'home',
    components: {
        RJDBox,Loading
    },
    data () {
        return {
            loading : false,
            playlist: {},
            newMusic: {},
            hotTracks: {},
            moreMusic: {},
            slider1 : {},
            slider2 : {},
            slider3 : {},
            slider4 : {}
        }
    },
    methods: {
        getLatest () {
            let self = this;
            self.loading = true;
            axios.get(window.API_URL+'/home_items?v=2')
                .then(function (response) {
                    // handle success
                    response.data.sections.forEach(function(index) {
                        switch (index.id) {
                            case 'home_slider' :
                                self.slider1 = index.items;
                                break;
                            case 'home_slider2' :
                                self.slider2 = index.items;
                                break;
                            case 'home_slider3' :
                                self.slider3 = index.items;
                                break;
                            case 'home_slider4' :
                                self.slider4 = index.items;
                                break;
                            case 'new_playlists' :
                                self.playlist = index.items;
                                break;
                            case 'home_grid' :
                                self.newMusic = index.items;
                                break;
                            case 'featured_mp3s_choice' :
                                self.hotTracks = index.items;
                                break;
                            case 'home_grid_ads' :
                                self.moreMusic = index.items;
                                break;
                        }
                    });
                    self.loading = false;
                })
                .catch(function (error) {
                    console.log(error);
                    self.loading = false;
                });
        }
    },
    created () {
        this.getLatest();
    }
}
</script>