<template>
    <main>
        <loading v-if="loading"></loading>
        <div class="container-fluid mt-4" v-if="fulldata.items && fulldata.items.length > 0" :style="backgroundStyle">
            <div class="row mb-4">
                <div class="col-xl-1 col-lg-2 col-md-3 col-sm-4 col-6">
                    <img v-lazy="fulldata.thumbnail" :alt="fulldata.title" class="img-fluid">
                </div>
                <div class="col">
                    <h3 class="pt-0 pt-md-3" v-text="fulldata.title"></h3>
                    <span style="font-size: 12px" v-html="fulldata.followers+' Followers'"></span> | <span style="font-size: 12px" v-html="fulldata.created_by"></span> | <span style="font-size: 12px;" v-html="fulldata.subtype === 'mp3' ? fulldata.count + ' Songs' : fulldata.count + ' Videos'"></span>
                </div>
            </div>
            <div class="row">
                <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in fulldata.items" :key="'Playlist'+index">
                    <RJDBox :song="data.song" :artist="data.artist" :img="data.photo" :tag="data.type" :data="data"></RJDBox>
                </div>
            </div>
        </div>
    </main>
</template>

<script>
import RJDBox from './RJDBox.vue';
import Loading from './Loading';
import axios from 'axios';

export default {
    name: 'playlist',
    components: {
        RJDBox,Loading
    },
    data () {
        return {
            loading : false,
            fulldata : {},
            backgroundStyle : {}
        }
    },
    watch: {
        '$route': 'getLatest'
    },
    methods: {
        getLatest () {
            let self = this;
            self.loading = true;
            axios.get(window.API_URL+'/mp3_playlist_with_items?id='+this.$route.params.id)
                .then(function (response) {
                    // handle success
                    self.fulldata = response.data;
                    self.backgroundStyle = {background: ['#000000', '-moz-linear-gradient(top, #000000 0%, ' + response.data.bg_color + ' 50%, #000000 100%)', '-webkit-linear-gradient(top, #000000 0%,' + response.data.bg_color + ' 50%,#000000 100%)' , 'linear-gradient(to bottom, #000000 0%,' + response.data.bg_color +' 50%,#000000 100%)']};
                    self.loading = false;
                })
                .catch(function (error) {
                    // eslint-disable-next-line
                    console.log(error);
                    self.loading = false;
                });
        }
    },
    created () {
        this.getLatest();
    }
}
</script>