<template>
    <div id="podcast" class="mt-4">
        <b-tabs pills v-model="tabIndex">
            <b-tab title="Featured" active>
                <loading v-if="loading && !featured.length > 0"></loading>
                <div class="container-fluid mt-4" v-if="featured && featured.length > 0">
                    <div class="row">
                        <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in featured" :key="'Featured'+index">
                            <RJDBox :song="data.date" :artist="data.title" :img="data.photo" :tag="'podcast'" :data="data"></RJDBox>
                        </div>
                    </div>
                </div>
            </b-tab>
            <b-tab title="Popular">
                <loading v-if="loading && !popular.length > 0"></loading>
                <div class="container-fluid mt-4" v-if="popular && popular.length > 0">
                    <div class="row">
                        <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in popular" :key="'Popular'+index">
                            <RJDBox :song="data.date" :artist="data.title" :img="data.photo" :tag="'podcast'" :data="data"></RJDBox>
                        </div>
                    </div>
                </div>
            </b-tab>
            <b-tab title="Dance">
                <loading v-if="loading && !dance.length > 0"></loading>
                <div class="container-fluid mt-4" v-if="dance && dance.length > 0">
                    <div class="row">
                        <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in dance" :key="'Dance'+index">
                            <RJDBox :song="data.date" :artist="data.title" :img="data.photo" :tag="'podcast'" :data="data"></RJDBox>
                        </div>
                    </div>
                </div>
            </b-tab>
            <b-tab title="Shows">
                <loading v-if="loading && !shows.length > 0"></loading>
                <div class="container-fluid mt-4" v-if="shows && shows.length > 0">
                    <div class="row">
                        <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in shows" :key="'Shows'+index">
                            <RJDBox :song="data.date" :artist="data.title" :img="data.photo" :tag="'podcast'" :data="data"></RJDBox>
                        </div>
                    </div>
                </div>
            </b-tab>
        </b-tabs>
    </div>
</template>

<script>
import RJDBox from './RJDBox.vue';
import Loading from './Loading';
import axios from 'axios';

    export default {
        name: "Podcast",
        components: {
            RJDBox,Loading
        },
        data () {
            return {
                loading : false,
                tabIndex: 0,
                featured: {},
                popular: {},
                dance: {},
                shows: {}
            }
        },
        methods: {
            getFeatured () {
                let self = this;
                self.loading = true;
                axios.get(window.API_URL+'/podcasts?type=featured')
                    .then(function (response) {
                        // handle success
                        self.featured = response.data;
                        self.loading = false;
                    })
                    .catch(function (error) {
                        console.log(error);
                        self.loading = false;
                    });
            },
            getPopular () {
                let self = this;
                self.loading = true;
                axios.get(window.API_URL+'/podcasts?type=popular')
                    .then(function (response) {
                        // handle success
                        self.popular = response.data;
                        self.loading = false;
                    })
                    .catch(function (error) {
                        console.log(error);
                        self.loading = false;
                    });
            },
            getDance () {
                let self = this;
                self.loading = true;
                axios.get(window.API_URL+'/podcasts?type=dance')
                    .then(function (response) {
                        // handle success
                        self.dance = response.data;
                        self.loading = false;
                    })
                    .catch(function (error) {
                        console.log(error);
                        self.loading = false;
                    });
            },
            getShows () {
                let self = this;
                self.loading = true;
                axios.get(window.API_URL+'/podcasts?type=shows')
                    .then(function (response) {
                        // handle success
                        self.shows = response.data;
                        self.loading = false;
                    })
                    .catch(function (error) {
                        console.log(error);
                        self.loading = false;
                    });
            }
        },
        watch: {
            tabIndex() {
                switch (this.tabIndex) {
                    case 0 :
                        this.getFeatured();
                        break;
                    case 1 :
                        this.getPopular();
                        break;
                    case 2 :
                        this.getDance();
                        break;
                    case 3 :
                        this.getShows();
                        break;
                    default :
                        this.getFeatured();
                        break;
                }
            }
        },
        created () {
            this.getFeatured();
        }
    }
</script>