<template>
    <div>
        <div class="container mt-3">
            <div class="row">
                <div class="col-md-2"></div>
                <div class="col-md-8 col-12">
                    <div id="search">
                        <input type="text" @input="searchBy()" v-model="searchText" class="form-control" autocomplete="off" placeholder="Name, artist, keyword or lyric">
                    </div>
                </div>
            </div>
        </div>
        <loading v-if="loading && searchText"></loading>
        <div class="container-fluid mt-4" v-if="searchText">
            <div id="songs" v-if="searchMp3 && searchMp3.length > 0">
                <div class="row">
                    <div class="col">
                        <h3 class="mb-3">SONGS</h3>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in searchMp3.slice(0, 12)" :key="'Mp3'+index">
                        <RJDBox :img="data.thumbnail" :song="data.song" :artist="data.artist" :tag="data.type" :data="data"></RJDBox>
                    </div>
                </div>
            </div>
            <div id="albums" v-if="searchAlbum && searchAlbum.length > 0">
                <div class="row">
                    <div class="col">
                        <h3 class="mb-3">ALBUMS</h3>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in searchAlbum.slice(0, 12)" :key="'Album'+index">
                        <RJDBox @click.native="linkClicked" :song="data.album_album" :artist="data.album_artist" :img="data.photo" :tag="'mp3_album'" :data="data"></RJDBox>
                    </div>
                </div>
            </div>
            <div id="podcasts" v-if="searchPodcast && searchPodcast.length > 0">
                <div class="row">
                    <div class="col">
                        <h2 class="mb-3">PODCASTS</h2>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in searchPodcast.slice(0, 12)" :key="'Podcast'+index">
                        <RJDBox :song="data.date" :artist="data.title" :img="data.thumbnail" :tag="data.type" :data="data"></RJDBox>
                    </div>
                </div>
            </div>
            <div id="lyrics" v-if="searchLyric && searchLyric.length > 0">
                <div class="row">
                    <div class="col">
                        <h2 class="mb-3">LYRICS</h2>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in searchLyric.slice(0, 12)" :key="'Lyric'+index">
                        <RJDBox :song="data.song" :artist="data.artist" :img="data.thumbnail" :tag="data.type" :data="data"></RJDBox>
                    </div>
                </div>
            </div>
            <div id="videos" v-if="searchVideos && searchVideos.length > 0">
                <div class="row">
                    <div class="col">
                        <h2 class="mb-3">VIDEOS</h2>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-2 col-md-3 col-sm-6 col-6 mb-4" v-for="(data,index) in searchVideos.slice(0, 12)" :key="'Video'+index">
                        <RJDBox :song="data.song" :artist="data.artist" :img="data.photo" :tag="data.type" :data="data"></RJDBox>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import RJDBox from './RJDBox';
import Loading from './Loading';
import axios from 'axios';
let timeout = null;

export default {
    name: 'search',
    components: {
        RJDBox,Loading
    },
    data () {
        return {
            loading : false,
            searchText: '',
            searchMp3: {},
            searchAlbum: {},
            searchLyric: {},
            searchPodcast: {},
            searchVideos: {}
        }
    },
    methods : {
        searchBy () {
            clearTimeout(timeout);
            let self = this;
            this.$emit('searchText', this.searchText);
            self.loading = true;
            self.searchMp3 = {};
            self.searchAlbum = {};
            self.searchLyric = {};
            self.searchPodcast = {};
            self.searchVideos = {};
            timeout = setTimeout(() => {
                axios.post(window.API_URL+'/search', {
                    query: this.searchText
                })
                    .then(function (response) {
                        self.searchMp3 = response.data.mp3s;
                        self.searchAlbum = response.data.albums;
                        self.searchLyric = response.data.lyrics;
                        self.searchPodcast = response.data.podcasts;
                        self.searchVideos = response.data.videos;
                        self.loading = false;
                    })
                    .catch(function (error) {
                        // eslint-disable-next-line
                        console.log(error);
                        self.loading = false;
                    });
            }, 300);
        },
        linkClicked() {
            this.searchText = '';
            this.$emit('searchText', this.searchText);
        }
    }
}
</script>

